import { HeadFC, HeadProps, PageProps, graphql } from "gatsby";
import React from "react";
import ContentContainer from "../components/common/contentContainer";
import { HeadSeo } from "../components/common/metadata";
import { IRevitPageContext, IRevitQueryResult } from "../types/revit.type";

const RevitPage = ({
  data, pageContext
}: PageProps<IRevitQueryResult, IRevitPageContext>) => {
  const elements = data.allMarkdownRemark.nodes;
  const { markupData } = pageContext;

  return (
    <>
      <div className="flex bg-main-100">
        <ContentContainer
          maxWidthClass="max-w-2xl"
          className="flex-col text-secondary-100 text-lg pt-8 sm:pt-16">
          <div className="text-center">
            <h1 className="text-secondary-100 text-2xl sm:text-4xl">{markupData.head}</h1>
            <p className="">{markupData.subhead}</p>
            <h2 className="text-[#5F20FE] text-2xl mt-4">{markupData.title}</h2>
          </div>

          <div className="mx-auto py-4 max-w-4xl flex flex-col gap-10">
            {elements.map((e) => (
              <div key={e.frontmatter.link} id={e.frontmatter.link}>
                <a href={`#${e.frontmatter.link}`} className="block mb-2">
                  <h2 className="text-2xl text-center">
                    {e.frontmatter.title}
                  </h2>
                </a>
                <article
                  className="single-post text-main-100 bg-secondary-100 revit p-10"
                  dangerouslySetInnerHTML={{ __html: e.html }}
                  itemProp="articleBody"
                />
              </div>
            ))}
          </div>
        </ContentContainer>
      </div>
    </>
  );
};

export default RevitPage;

export const Head: HeadFC<IRevitQueryResult, IRevitPageContext> = ({
  pageContext: { markupData, lang, site },
  location,
}: HeadProps<IRevitQueryResult, IRevitPageContext>) => (
  <HeadSeo
    title={`${markupData.head} | ${markupData.subhead.toUpperCase()}`}
    siteUrl={site.siteUrl}
    description={site.description}
    path={location.pathname}
    lang={lang}
  />
);

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "revit" }, language: { eq: "en" } }
      }
    ) {
      nodes {
        id
        html
        frontmatter {
          title
          link
        }
      }
    }
  }
`;
